<template>
  <div class="wrap wrap-freight-list">
     
    <!--面包导航-->
    <div class="nav-bar clear-fix">
      <el-breadcrumb separator=">" class="fl-l">
        <el-breadcrumb-item>运费模板管理</el-breadcrumb-item>
        <el-breadcrumb-item>运费模板列表</el-breadcrumb-item>
      </el-breadcrumb>
      <!--新增操作-->
        <div class="add-menu fl-r" >
            <el-button type="warning" class="add-menu-btn" @click="doAction('add')" size="mini">
                <i class="iconfont icon-add"></i>新增运费模板</el-button>
        </div>
    </div>

    <div class="batch">
        <!-- <el-button type="primary" size="small" @click="goodsDelete('batch')">清空回收站</el-button> -->
    </div>
    <!--列表-->
    <el-table :data="freightList" stripe  v-loading="loading" class="report goods-list" max-height="520"   @selection-change="handleSelectionChange" style="width:100%;" border>
      <el-table-column type="selection" width="80" ></el-table-column>
      <el-table-column type="index" width="70"></el-table-column>    
      <el-table-column label="模板名称" prop="templateTitle" align="center" min-width="150" :show-overflow-tooltip="true"  >
      </el-table-column>
      <el-table-column label="仓库名称" prop="warehouseName" align="center" min-width="150" :show-overflow-tooltip="true"  >
      </el-table-column>
      <el-table-column label="创建时间" align="center" width="160">
        <template slot-scope="scope">{{scope.row.createTime }}</template>
      </el-table-column>
      <el-table-column label="最后修改时间" align="center" width="160">
        <template slot-scope="scope">{{scope.row.updateTime }}</template>
      </el-table-column>
           
      <el-table-column label="操作"  align="left" min-width="150" :show-overflow-tooltip="true" >
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click=" doAction('edit',scope.row.id,scope.row.supplierId)">编辑</el-button>       
          <el-button type="danger" size="mini" @click=" doAction('delete',scope.row.id)"  :disabled="scope.row.supplierId=='0'">删除</el-button>       
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1"> 
      <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.pageNum"  :page-size="searchForm.pageSize" layout="total,  prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

  </div>
</template>

<style lang="less" scoped>
.wrap-freight-list{
   padding-right: 15px;
}

</style>

<script>
import format from "date-format";
export default {
   
    data(){
        return {
            loading:false,
            searchForm: {
                pager: {
                    page: 1,
                    pageSize: 1000,
                    isSort: true,
                    order: "desc",
                    sort: "create_time"
                },
                
            },
            freightList:[],
            total:'',   
           
        }
    },
    filters: {
        dateFormat(date) {
            return date ? format("yyyy-MM-dd hh:mm", new Date(date)) : "";
        }
    },
    methods:{
        getfreightList(){
            this.loading = true;
            this.axios.post("/proxy/freight/api/template/list", {body: this.searchForm}).then(response => {
                this.loading = false;
                let data = response.data;
                if (data.head.code == "10000") {
                    let arr=JSON.parse(JSON.stringify(data.body.rows));
                    let tmp=[];
                    for(let i in arr){
                        if(arr[i].supplierId!='0'){
                           tmp.push(arr[i]);     
                        }
                    }
                    this.freightList=tmp;
                }
            });
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.pageNum = val;
            this.getfreightList();
        },
        //复选框
        handleSelectionChange(val) {
            // this.selectData = val;
        },
       doAction(type,data,supplierId){
           switch(type){
               case 'add':            
                    this.$router.push('/vendor/freight/add');                   
                    break;
               case 'edit':
                    this.$router.push('/vendor/freight/'+data+'?supplierId='+supplierId);
                break;
                case 'delete':                   
                    this.$confirm('您确定要删除?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                        this.axios.post("/proxy/freight/api/template/remove", {body:{id:data}}).then(response => {    
                            let data = response.data;
                            if (data.head.code == "10000") {
                                this.$message({
                                    type: 'success',
                                    message: '删除成功!'
                                });
                                this.getfreightList();
                            }
                        });
                        
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });          
                    });
        
                break;
           }
       }
    },
    created(){
        this.getfreightList();
        
    }
  
}
</script>



